:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-topology-list-view {
  --odc-topology-list-view-type-icon-size: 16px;
  --odc-topology-list-view-sidebar-width: 550px;
  --pf-v5-c-data-list__item--m-selectable--OutlineOffset: 0;
  background-color: var(--pf-v5-global--BackgroundColor--100);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: auto;
  padding-left: var(--pf-v5-global--spacer--sm);
  -webkit-overflow-scrolling: touch;
}

.odc-m-filter-active .odc-topology-list-view .odc-topology-list-view__kind-row,
.odc-m-filter-active .odc-topology-list-view .odc-topology-list-view__item-row,
.odc-m-filter-active .odc-topology-list-view .odc-topology-list-view__application-row {
  opacity: .5;
}

.odc-m-filter-active .odc-topology-list-view .odc-topology-list-view__kind-row.is-filtered,
.odc-m-filter-active .odc-topology-list-view .odc-topology-list-view__item-row.is-filtered,
.odc-m-filter-active .odc-topology-list-view .odc-topology-list-view__application-row.is-filtered {
  opacity: 1;
}

.odc-topology-list-view .pf-v5-c-data-list {
  --pf-v5-c-data-list__item-row--PaddingLeft: calc( var(--pf-v5-c-data-list__item-row--xl--PaddingLeft) - var(--pf-v5-global--spacer--sm) );
}

.odc-topology-list-view__sidebar {
  background-color: var(--pf-v5-global--BackgroundColor--100);
  bottom: 0;
  box-shadow: var(--pf-v5-global--BoxShadow--md);
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  width: calc(100% - 15px);
  z-index: 5;
}

@media (min-width: 768px) {
  .odc-topology-list-view__sidebar {
    max-width: var(--odc-topology-list-view-sidebar-width);
  }
}

.odc-topology-list-view__sidebar-appear {
  opacity: 0;
  transform: translateX(10%);
}

.odc-topology-list-view__sidebar-appear-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 175ms ease-out,transform 225ms ease-out;
}

.odc-topology-list-view__group-resource-count {
  margin-right: var(--pf-v5-global--spacer--md);
}

.odc-topology-list-view__group-resource-count .co-m-resource-icon {
  margin-left: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__application-label-cell {
  display: inline;
  vertical-align: middle;
}

.odc-topology-list-view__application-label-cell .odc-topology-list-view__resource-icon__container {
  margin-top: 8px;
}

.odc-topology-list-view__label-cell {
  display: inline-flex;
  align-items: start;
}

.odc-topology-list-view__alert-button {
  padding: 2px var(--pf-v5-global--spacer--xs);
  margin-left: var(--pf-v5-global--spacer--xs);
}

.odc-topology-list-view__resource-icon-cell.pf-v5-c-data-list__cell {
  margin-right: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__resource-icon__container {
  display: flex;
  align-items: center;
  margin-right: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__resource-icon {
  margin: 2px 0;
}

.odc-topology-list-view__type-icon-bg {
  background-color: var(--pf-v5-global--palette--white);
  border: solid 1px var(--pf-v5-global--BorderColor--300);
  border-radius: 12px;
  box-shadow: 2px 2px 3px var(--pf-v5-global--BorderColor--100);
  margin-left: var(--pf-v5-global--spacer--xs);
  padding: 0 3px 1px;
}

.odc-topology-list-view__type-icon {
  font-size: var(--odc-topology-list-view-type-icon-size);
  height: var(--odc-topology-list-view-type-icon-size);
  min-width: var(--odc-topology-list-view-type-icon-size);
}

.odc-topology-list-view__application {
  margin-top: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__application-row .pf-v5-c-data-list__item-content {
  padding-bottom: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__application-row .pf-v5-c-data-list__item-content .pf-v5-c-data-list__cell {
  padding-top: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__application-row.is-filtered {
  outline-offset: -5px;
  outline: #ef9234 auto 1px;
}

.odc-topology-list-view__application-label {
  font-size: var(--pf-v5-global--FontSize--2xl);
  margin-left: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__unassigned-label {
  font-size: var(--pf-v5-global--FontSize--2xl);
}

.odc-topology-list-view__kind-row,
.odc-topology-list-view__item-row {
  border-bottom: var(--pf-v5-c-data-list__item--BorderBottomWidth) solid var(--pf-v5-c-data-list__item--BorderBottomColor);
  min-height: 58px;
}

.odc-topology-list-view__kind-row.is-filtered,
.odc-topology-list-view__item-row.is-filtered {
  outline-offset: -5px;
  outline: #ef9234 auto 1px;
}

.odc-topology-list-view__kind-label {
  font-size: var(--pf-v5-global--FontSize--lg);
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable .odc-topology-list-view__kind-row.pf-v5-c-data-list__item-row:hover,
.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable .odc-topology-list-view__unassigned-group.pf-v5-c-data-list__item-row:hover {
  background-color: var(--pf-v5-global--BackgroundColor--100);
  cursor: default;
}

.odc-topology-list-view .odc-topology-list-view__group-children .odc-topology-list-view__item-row {
  padding-left: var(--pf-v5-global--spacer--2xl);
}

.odc-topology-list-view .odc-topology-list-view__group-children .odc-topology-list-view__group-children .odc-topology-list-view__item-row {
  padding-left: var(--pf-v5-global--spacer--3xl);
}

.odc-topology-list-view .odc-topology-list-view__group-children .odc-topology-list-view__group-children .odc-topology-list-view__group-children .odc-topology-list-view__item-row {
  padding-left: var(--pf-v5-global--spacer--4xl);
}

.odc-topology-list-view .pf-v5-c-data-list {
  --pf-v5-c-data-list__item--m-selected--BoxShadow: none;
  --pf-v5-c-data-list__item--m-selectable--hover--BoxShadow: none;
  --pf-v5-c-data-list__item--m-selectable--focus--BoxShadow: none;
  --pf-v5-c-data-list__item--m-selectable--active--BoxShadow: none;
  --pf-v5-c-data-list__expandable-content--BorderTopWidth: 0;
  --pf-v5-c-data-list--BorderTopWidth: 0;
}

.odc-topology-list-view .pf-v5-c-data-list__expandable-content {
  overflow-y: hidden;
}

.odc-topology-list-view .pf-v5-c-data-list__expandable-content .pf-v5-c-data-list__expandable-content-body {
  --pf-v5-c-data-list__expandable-content-body--PaddingTop: 0;
  --pf-v5-c-data-list__expandable-content-body--PaddingRight: 0;
  --pf-v5-c-data-list__expandable-content-body--PaddingBottom: 0;
  --pf-v5-c-data-list__expandable-content-body--PaddingLeft: 0;
}

.odc-topology-list-view .pf-v5-c-data-list__item {
  border-bottom: 0;
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-expanded {
  --pf-v5-c-data-list__item--before--BackgroundColor: transparent;
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-expanded.pf-m-clickable:not(.pf-m-selected) {
  --pf-v5-c-data-list__item--before--BackgroundColor: transparent;
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-expanded.pf-m-clickable.pf-m-selected {
  --pf-v5-c-data-list__item--before--BackgroundColor: var( --pf-v5-c-data-list__item--m-expanded--before--BackgroundColor );
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-expanded:focus {
  outline-color: transparent;
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-expanded:focus>.odc-topology-list-view__item-row {
  outline-offset: -5px;
  outline: -webkit-focus-ring-color auto 1px;
  background-color: var(--pf-v5-global--BackgroundColor--200);
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-selected,
.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable:hover,
.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable:focus {
  z-index: initial;
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable:hover:not(.pf-m-selected):not(:last-child),
.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable:focus:not(.pf-m-selected):not(:last-child) {
  --pf-v5-c-data-list__item--BorderBottomWidth: 1px;
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable:hover:not(.pf-m-selected):not(:last-child)+.pf-v5-c-data-list__item,
.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable:focus:not(.pf-m-selected):not(:last-child)+.pf-v5-c-data-list__item {
  border-top: 0;
}

.odc-topology-list-view .pf-v5-c-data-list__item.pf-m-clickable .pf-v5-c-data-list__item-row:hover {
  background-color: var(--pf-v5-global--BackgroundColor--200);
}

.odc-topology-list-view .pf-v5-c-data-list__item-content {
  --pf-v5-c-data-list__item-content--md--PaddingBottom: var(--pf-v5-global--spacer--md);
}

.odc-topology-list-view .pf-v5-c-data-list__cell,
.odc-topology-list-view .pf-v5-c-data-list__cell.pf-m-icon+.pf-v5-c-data-list__cell {
  padding-top: var(--pf-v5-global--spacer--md);
}

.odc-topology-list-view .pf-v5-c-data-list__expandable-content .pf-v5-c-data-list__expandable-content-body>.pf-v5-c-data-list {
  margin-left: 0;
  margin-inline-start: 0;
}

.odc-topology-list-view .pf-v5-c-data-list__expandable-content .pf-v5-c-data-list__expandable-content-body .pf-v5-c-data-list__item-row {
  --pf-v5-c-data-list__item-row--PaddingLeft: var(--pf-global--spacer--lg);
}

