:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-topology-list-view__alert-cell {
  min-width: 0;
}

@media (max-width: 991px) {
  .odc-topology-list-view__alert-cell {
    grid-row: 2;
  }
}

@media (min-width: 992px) {
  .odc-topology-list-view__alert-cell {
    grid-area: alert;
  }
}

.odc-topology-list-view__alert-cell--status .co-icon-and-text {
  display: inline-flex;
  margin-right: var(--pf-v5-global--spacer--sm);
}

.odc-topology-list-view__alert-cell__label {
  margin-right: var(--pf-v5-global--spacer--sm);
}

