.odc-topology {
  display: grid;
  height: 100%;
}

.odc-topology__empty-state {
  padding-top: 0;
  position: absolute;
  top: 15%;
}

.odc-topology .pf-topology-container__with-sidebar .pf-topology-content {
  position: relative;
}

.odc-topology__hint-container {
  justify-content: center;
  display: flex;
  pointer-events: none;
  position: absolute;
  top: var(--pf-v5-global--spacer--sm);
  left: 0;
  right: 0;
}

.odc-topology__hint-background {
  background-color: var(--pf-v5-global--BackgroundColor--100);
  border: 1px solid var(--pf-v5-global--BorderColor--light-100);
  border-radius: 8px;
  padding: var(--pf-v5-global--spacer--xs) var(--pf-v5-global--spacer--sm);
  pointer-events: none;
}

@media (min-width: 768px) {
  .odc-topology__view-switcher.pf-m-plain {
    padding-bottom: 4px;
    padding-right: 2px;
  }
}

.odc-topology__layout-group {
  display: flex;
  align-items: center;
  margin: 0 var(--pf-v5-global--spacer--sm);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-v5-c-button {
  --pf-v5-c-button--BorderRadius: 0;
  margin-right: 0;
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-v5-c-button:focus {
  z-index: 10;
  color: var(--pf-v5-global--active-color--400);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-v5-c-button.pf-m-active {
  background-color: var(--pf-v5-global--Color--light-300);
  color: var(--pf-v5-global--icon--Color--dark);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-v5-c-button:hover {
  z-index: 20;
  color: var(--pf-v5-global--active-color--400);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-v5-c-button:hover.pf-m-active {
  color: var(--pf-v5-global--icon--Color--dark);
}

.odc-topology__layout-button:first-of-type .pf-topology-control-bar__button.pf-v5-c-button {
  --pf-v5-c-button--BorderRadius: var(--pf-v5-global--BorderRadius--sm) 0 0 var(--pf-v5-global--BorderRadius--sm);
}

.odc-topology__layout-button:last-of-type .pf-topology-control-bar__button.pf-v5-c-button {
  --pf-v5-c-button--BorderRadius: 0 var(--pf-v5-global--BorderRadius--sm) var(--pf-v5-global--BorderRadius--sm) 0;
}

.odc-topology__layout-button__icon {
  margin-right: var(--pf-v5-global--spacer--xs);
}

.odc-topology__shortcuts-button {
  margin-right: var(--pf-v5-global--spacer--2xl);
}

.odc-topology__dropzone {
  background-color: rgba(0,102,204,.1) !important;
  color: var(--pf-v5-global--palette--black-900) !important;
  margin: 0 !important;
}

.odc-topology__dropzone-text {
  top: 0%;
  padding-top: var(--pf-v5-global--spacer--lg);
}

.odc-topology .pf-topology-container {
  display: flex;
}

.odc-topology .pf-topology-side-bar {
  display: flex;
  flex-direction: column;
}

.odc-topology .pf-topology-side-bar-resizable {
  flex: 1 1 0;
}

.odc-topology .pf-topology-side-bar__body {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.odc-topology .pf-topology-side-bar__body .resource-overview {
  flex: 1;
}

.odc-topology .pf-topology-side-bar__body .overview__sidebar-pane-body {
  min-height: initial;
}

