:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-topology-list-view__metrics-cell__metric-tooltip {
  display: flex;
  min-width: 225px;
}

.odc-topology-list-view__metrics-cell__tooltip-title {
  margin-bottom: 5px;
}

.odc-topology-list-view__metrics-cell__tooltip-name {
  flex: 2;
  overflow: hidden;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.odc-topology-list-view__metrics-cell__metric-tooltip-value {
  flex: 1;
  text-align: right;
}

.odc-topology-list-view__metrics-cell__metric-value {
  font-size: 18px;
}

.odc-topology-list-view__metrics-cell__metric-unit {
  color: #6a6e73;
  font-size: 13px;
  margin-left: 1px;
}

.odc-topology-list-view__metrics-cell__detail--memory {
  min-width: 0;
}

@media (min-width: 992px) {
  .odc-topology-list-view__metrics-cell__detail--memory {
    grid-area: memory;
  }
}

.odc-topology-list-view__metrics-cell__detail--cpu {
  min-width: 0;
}

@media (min-width: 992px) {
  .odc-topology-list-view__metrics-cell__detail--cpu {
    grid-area: cpu;
  }
}

@media (min-width: 992px) {
  .odc-topology-list-view__metrics-cell__detail--cpu,
  .odc-topology-list-view__metrics-cell__detail--memory {
    text-align: right;
  }
}

