:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-kind-filter-dropdown__clear-button {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

.odc-kind-filter-dropdown__kind-count {
  background-color: var(--pf-v5-global--BorderColor--300);
  border-radius: 20px;
  display: inline-block;
  line-height: 16px;
  margin-left: var(--pf-v5-global--spacer--sm);
  min-width: 18px;
  padding: 1px 4px;
  text-align: center;
  white-space: nowrap;
}

