:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-topology-list-view__detail--status {
  text-align: right;
}

@media (min-width: 992px) {
  .odc-topology-list-view__detail--status {
    grid-area: status;
  }
}

.odc-topology-list-view__detail--status .co-icon-and-text {
  justify-content: flex-end;
}

@media (min-width: 992px) {
  .odc-topology-list-view--sidebar-shown .odc-topology-list-view__detail--status {
    grid-column: 2;
    margin-right: 40px;
  }
}

