:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-topology-filter-dropdown__expand-groups-switcher {
  display: flex;
  align-items: center;
}

.odc-topology-filter-dropdown__expand-groups-switcher .pf-v5-c-menu__group-title {
  flex: 1;
  padding-block-start: var(--pf-v5-global--spacer--sm);
  padding-block-end: var(--pf-v5-global--spacer--sm);
}

.odc-topology-filter-dropdown__expand-groups-switcher .pf-v5-c-switch {
  margin-right: var(--pf-v5-global--spacer--sm);
}

.odc-topology-filter-dropdown__expand-groups-switcher .pf-m-disabled {
  color: var(--pf-v5-c-check__label--disabled--Color);
}

.odc-topology-filter-dropdown .pf-v5-c-select {
  --pf-v5-c-select__menu-group-title--PaddingBottom: 0;
}

.odc-topology-filter-dropdown__expand-groups-label>.pf-v5-c-select__menu-group-title {
  padding-top: 0;
}

